<template>
  <div>
    <el-date-picker v-model="month" type="month" placeholder="选择月" style="display: block;"></el-date-picker>
    <div class="flex-between">
      <div class="left">
        <p class="text-center caption-color">一部团队 pipeline 完成情况</p>
        <template v-for="(el, idx) in teamOne">
          <el-table :data="el" border style="width: 100%" :span-method="arrSpanMethod" 
            :header-cell-style="hdStyle" :cell-style="cellStyle" :key="idx">
            <el-table-column v-for="(item ,index) in tableCloumn2" :key="index" :prop="item.prop" :label="item.label"/>
          </el-table>
        </template>
      </div>
      <div class="right">
        <p class="text-center caption-color">二部团队 pipeline 完成情况</p>
        <template v-for="(el, idx) in teamTwo">
          <el-table :data="el" border style="width: 100%" :span-method="arrSpanMethod" 
            :header-cell-style="hdStyle" :cell-style="cellStyle" :key="idx">
            <el-table-column v-for="(item ,index) in tableCloumn2" :key="index" :prop="item.prop" :label="item.label"/>
          </el-table>
        </template>
      </div>
    </div>

  </div>
</template>

<script>
import cellStyle from '../mixin/cellStyle'
export default {
  mixins: [cellStyle],
  data() {
    return {
      month: new Date(),
      tableCloumn2: [
        { prop: 'col1', label: '姓名' },
        { prop: 'col2', label: '指标' },
        { prop: 'col3', label: '权重' },
        { prop: 'col4', label: '应完成/花费' },
        { prop: 'col5', label: '实际完成/花费' },
        { prop: 'col6', label: '得分' },
      ],
      teamOne: [
        [
          { col1: '豆纪锋', col2: '任务额完成比例', col3: '', col4: '', col5: '', col6: ''},
          { col1: '豆纪锋', col2: '费用额度', col3: '', col4: '', col5: '', col6: ''},
          { col1: '豆纪锋', col2: '销售活动', col3: '', col4: '', col5: '', col6: ''},
          { col1: '豆纪锋', col2: '新建机会', col3: '', col4: '', col5: '', col6: ''},
          { col1: '豆纪锋', col2: '本月打卡/签到', col3: '', col4: '', col5: '', col6: ''},
          { col1: '豆纪锋', col2: '总计得分', col3: '', col4: '', col5: '', col6: ''},
        ],
        [
          { col1: '程池旭', col2: '任务额完成比例', col3: '', col4: '', col5: '', col6: ''},
          { col1: '程池旭', col2: '费用额度', col3: '', col4: '', col5: '', col6: ''},
          { col1: '程池旭', col2: '销售活动', col3: '', col4: '', col5: '', col6: ''},
          { col1: '程池旭', col2: '新建机会', col3: '', col4: '', col5: '', col6: ''},
          { col1: '程池旭', col2: '本月打卡/签到', col3: '', col4: '', col5: '', col6: ''},
          { col1: '程池旭', col2: '总计得分', col3: '', col4: '', col5: '', col6: ''},
        ],
      ],
      teamTwo: [
        [
          { col1: '周合', col2: '任务额完成比例', col3: '', col4: '', col5: '', col6: ''},
          { col1: '周合', col2: '费用额度', col3: '', col4: '', col5: '', col6: ''},
          { col1: '周合', col2: '销售活动', col3: '', col4: '', col5: '', col6: ''},
          { col1: '周合', col2: '新建机会', col3: '', col4: '', col5: '', col6: ''},
          { col1: '周合', col2: '本月打卡/签到', col3: '', col4: '', col5: '', col6: ''},
          { col1: '周合', col2: '总计得分', col3: '', col4: '', col5: '', col6: ''},
        ],
        [
          { col1: '李国栋', col2: '任务额完成比例', col3: '', col4: '', col5: '', col6: ''},
          { col1: '李国栋', col2: '费用额度', col3: '', col4: '', col5: '', col6: ''},
          { col1: '李国栋', col2: '销售活动', col3: '', col4: '', col5: '', col6: ''},
          { col1: '李国栋', col2: '新建机会', col3: '', col4: '', col5: '', col6: ''},
          { col1: '李国栋', col2: '本月打卡/签到', col3: '', col4: '', col5: '', col6: ''},
          { col1: '李国栋', col2: '总计得分', col3: '', col4: '', col5: '', col6: ''},
        ]
      ],
    }
  },
  methods: {
    arrSpanMethod({ row, column, rowIndex, columnIndex }) {
      if (columnIndex % this.tableCloumn2.length === 0) {
        if (rowIndex % this.teamOne[0].length === 0) {
          return [this.teamOne[0].length, 1]
        } else {
          return [0, 0]
        }
      }
    },
  }
}
</script>

<style scoped>
.left {
  width: 49%;
}
.right {
  width: 49%;
}
.el-table {
  margin-top: 20px;
}
</style>



